parent;\n  }\n}\n\n","@mixin subheader($include-color: true) {\n  font-size: $h6-font-size;\n  font-weight: $font-weight-bold;\n  text-transform: uppercase;\n  letter-spacing: .04em;\n  line-height: $h6-line-height;\n\n  @if ($include-color) {\n    color: $text-muted;\n  }\n}\n\n@mixin scrollbar($is-dark: false) {\n  &::-webkit-scrollbar {\n    width: 6px;\n    height: 6px;\n    transition: .3s background;\n  }\n\n  &::-webkit-scrollbar-thumb {\n    border-radius: 5px;\n\n    @if ($is-dark) {\n      background: transparent;\n    } @else {\n      background: $gray-400;\n    }\n  }\n\n  &::-webkit-scrollbar-corner {\n    background: transparent;\n  }\n\n  &:hover::-webkit-scrollbar-thumb {\n    background: $gray-500;\n\n    @if ($is-dark) {\n      background: mix(#ffffff, $dark, 20%);\n    } @else {\n      background: $gray-500;\n    }\n  }\n}\n\n\n@mixin autodark-image {\n  filter: brightness(0) invert(1);\n}\n\n@mixin dark-mode {\n  @if $enable-dark-mode {\n    $selector: &;\n\n    @media not print {\n      @if ($selector) {\n        .theme-dark & {\n          @content\n        }\n      } @else {\n        .theme-dark {\n          @content\n        }\n      }\n    }\n\n    @media not print and (prefers-color-scheme: dark) {\n      @if ($selector) {\n        .theme-dark-auto & {\n          @content\n        }\n      } @else {\n        .theme-dark-auto {\n          @content\n        }\n      }\n    }\n  }\n}","@mixin navbar-color($bg-color: transparent, $color: $navbar-light-color, $brand-color: $navbar-light-brand-color, $link-active-color: $navbar-light-active-color, $link-disabled-color: $navbar-light-disabled-color) {\n  background: $bg-color;\n  color: $color;\n\n  @include scrollbar;\n\n  .text-muted {\n    color: rgba($color, $text-muted-opacity * $text-muted-opacity) !important;\n  }\n\n  .navbar-brand {\n    color: $brand-color;\n\n    &:hover,\n    &:focus {\n      color: $brand-color;\n      opacity: .8;\n    }\n  }\n\n  .navbar-nav {\n    .nav-link {\n      color: $color;\n\n      &:hover,\n      &:focus {\n        color: $link-active-color;\n      }\n\n      &.disabled {\n        color: $link-disabled-color;\n      }\n    }\n\n    .show > .nav-link,\n    .active > .nav-link,\n    .nav-link.show,\n    .nav-link.active {\n      color: $link-active-color;\n    }\n  }\n\n  .navbar-toggler {\n    color: $brand-color;\n    border-color: transparent;\n  }\n\n  .navbar-text {\n    color: $color;\n\n    a,\n    a:hover,\n    a:focus {\n      color: $color;\n    }\n  }\n}\n\n@mixin navbar-dark {\n  @include navbar-color($dark, $navbar-dark-color, $navbar-dark-brand-color, $navbar-dark-active-color, $navbar-dark-disabled-color);\n  @include scrollbar(true);\n\n  .input-icon-addon {\n    color: rgba($dark-mode-text, .64);\n  }\n\n  .form-control {\n    border-color: transparent;\n    background-color: rgba(0, 0, 0, .1);\n\n    &::placeholder {\n      color: rgba($dark-mode-text, .64);\n    }\n  }\n}\n\n@mixin navbar-vertical-nav {\n  .navbar-collapse {\n    flex-direction: column;\n\n    [class*=\"container\"] {\n      flex-direction: column;\n      align-items: stretch;\n    }\n\n    .navbar-nav {\n      margin-left: -($container-padding-x);\n      margin-right: -($container-padding-x);\n\n      .nav-link {\n        padding: .5rem $container-padding-x;\n        justify-content: flex-start;\n      }\n    }\n\n    .dropdown-menu {\n      padding: 0;\n      background: transparent;\n      position: static;\n      color: inherit;\n      box-shadow: none;\n      border: none;\n      columns: 1;\n      min-width: 0;\n\n      .dropdown-item {\n        min-width: 0;\n        display: flex;\n        width: auto;\n        padding-left: add($container-padding-x, 1.5rem);\n      }\n    }\n\n    .dropdown-toggle:after {\n      margin-left: auto;\n    }\n\n    .nav-item.active:after {\n      border-bottom-width: 0;\n      border-left-width: 2px;\n      right: auto;\n      top: 0;\n      bottom: 0;\n    }\n  }\n}\n\n/**\nNavbar\n */\n.navbar 