$navbar-brand-image-height;\n}\n\n/**\nNavbar toggler\n */\n.navbar-toggler {\n  border: 0;\n  width: $navbar-brand-image-height;\n  height: $navbar-brand-image-height;\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.navbar-toggler-icon {\n  height: 2px;\n  width: 1.25em;\n  background: currentColor;\n  border-radius: 10px;\n  transition: $navbar-toggler-animation-time $navbar-toggler-animation-time top, $navbar-toggler-animation-time $navbar-toggler-animation-time bottom, $navbar-toggler-animation-time transform, 0s $navbar-toggler-animation-time opacity;\n  position: relative;\n\n  &:before,\n  &:after {\n    content: '';\n    display: block;\n    height: inherit;\n    width: inherit;\n    border-radius: inherit;\n    background: inherit;\n    position: absolute;\n    left: 0;\n    transition: inherit;\n  }\n\n  &:before {\n    top: -.45em;\n  }\n\n  &:after {\n    bottom: -.45em;\n  }\n\n  .navbar-toggler[aria-expanded=\"true\"] & {\n    transform: rotate(45deg);\n    transition: .3s top, .3s bottom, .3s .3s transform, 0s .3s opacity;\n\n    &:before {\n      top: 0;\n      transform: rotate(-90deg);\n    }\n\n    &:after {\n      bottom: 0;\n      opacity: 0;\n    }\n  }\n}\n\n/**\nNavbar light\n */\n.navbar-light {\n  box-shadow: inset 0 -1px 0 0 $border-color;\n  background-color: $white;\n}\n\n/**\nNavbar dark\n */\n.navbar-dark {\n  @include navbar-dark;\n}\n\n/**\nNavbar nav\n */\n.navbar-nav {\n  align-items: stretch;\n\n  .nav-item {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n  }\n}\n\n/**\nNavbar side\n */\n.navbar-side {\n  margin: 0;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-around;\n}\n\n/**\nNavbar vertical\n */\n@if $enable-navbar-vertical {\n  .navbar-vertical {\n    &.navbar-expand {\n      @each $breakpoint in map-keys($grid-breakpoints) {\n        $next: breakpoint-next($breakpoint, $grid-breakpoints);\n     