nset;\n  }\n\n  &:disabled,\n  &.disabled {\n    color: $text-muted;\n    user-select: none;\n  }\n\n  &[size] {\n    width: auto;\n  }\n}\n\n.form-control-light {\n  background-color: $gray-100;\n  border-color: transparent;\n}\n\n.form-control-dark {\n  background-color: rgba($black, .1);\n  color: $white;\n  border-color: transparent;\n\n  &:focus {\n    background-color: rgba($black, .1);\n    box-shadow: none;\n    border-color: rgba($white, .24);\n  }\n\n  &::placeholder {\n    color: rgba($white, .6);\n  }\n}\n\n.form-control-rounded {\n  border-radius: 10rem;\n}\n\n.form-control-flush {\n  padding: 0;\n  background: none !important;\n  border-color: transparent !important;\n  resize: none;\n  box-shadow: none !important;\n  line-height: inherit;\n}\n\n.form-footer {\n  margin-top: 2rem;\n}\n\n.form-fieldset {\n  padding: 1rem;\n  margin-bottom: 1rem;\n  background: $min-black;\n  border: 1px solid $border-color;\n  border-radius: $border-radius;\n}\n\n/**\nForm help\n */\n.form-help {\n  display: inline-flex;\n  font-weight: $font-weight-bold;\n  align-items: center;\n  justify-content: center;\n  width: 1.125rem;\n  height: 1.125rem;\n  font-size: .75rem;\n  color: $text-muted;\n  text-align: center;\n  text-decoration: none;\n  cursor: pointer;\n  user-select: none;\n  background: $min-white;\n  border-radius: 50%;\n  transition: .3s background-color, .3s color;\n\n  &:hover,\n  &[aria-describedby] {\n    color: #ffffff;\n    background: $primary;\n  }\n}\n\n/**\nForm switch\n */\n.form-check {\n  user-select: none;\n\n  &.form-check-highlight .form-check-input:not(:checked) ~ .form-check-label {\n    opacity: .64;\n  }\n}\n\n.form-check-input {\n  background-size: $form-check-input-width;\n  margin-top: .25rem;\n\n  .form-switch & {\n    transition: .3s background-color, .3s background-position;\n  }\n}\n\n.form-check-label {\n  line-height: $line-height-sm;\n  padding: .125rem 0;\n}\n\n.form-check-description {\n  color: $text-muted;\n  font-size: $h5-font-size;\n  line-height: $line-height-sm;\n}\n\n/**\nInput group\n */\n.input-group-link {\n  font-size: $h5-font-size;\n}\n\n.input-group-flat {\n  &:focus-within {\n    box-shadow: $input-focus-box-shadow;\n    border-radius: $input-border-radius;\n\n    .form-control,\n    .input-group-text {\n      border-color: $input-focus-border-color !important;\n    }\n  }\n\n  .form-control {\n    &:focus {\n      border-color: $input-border-color;\n      box-shadow: none;\n    }\n\n    &:not(:last-child) {\n      border-right: 0;\n    }\n\n    &:not(:first-child) {\n      border-left: 0;\n    }\n  }\n\n  .input-group-text {\n    background: transparent;\n    transition: $input-transition;\n\n    &:first-child {\n      padding-right: 0;\n    }\n\n    &:last-child {\n      padding-left: 0;\n    }\n  }\n}\n\n\n/**\nUpload files\n */\n.form-file-button {\n  margin-left: 0;\n  border-left: 0;\n}","/**\nIcon input\n */\n.input-icon {\n  position: relative;\n\n  .form-control:not(:last-child) {\n    padding-right: 2.5rem;\n  }\n\n  .form-control:not(:first-child) {\n    padding-left: 2.5rem;\n  }\n}\n\n.input-icon-addon {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  min-width: 2.5rem;\n  color: $text-muted;\n  pointer-events: none;\n  font-size: 1.2em;\n\n  &:last-child {\n    right: 0;\n    left: auto;\n  }\n}\n","/*\nColor Input\n */\n.form-colorinput {\n  position: relative;\n  display: inline-block;\n  margin: 0;\n  line-height: 1;\n  cursor: pointer;\n}\n\n.form-colorinput-input {\n  position: absolute;\n  z-index: -1;\n  opacity: 0;\n}\n\n.form-colorinput-color {\n  display: block;\n  width: 1.5rem;\n  height: 1.5rem;\n  color: #fff;\n  border: 1px solid $border-color;\n  border-radius: 3px;\n  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .05);\n\n  &:before {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    content: \"\";\n    background: no-repeat